import React, { Component } from 'react'
import { Link } from 'react-router-dom'

var DisplayResults = (props) => {
    if (props.city === "home") { //jedna sa o klasicky kviz
        if (props.target.state.results.length === 1) {
            var res = props.target.state.results[0]
            return (<h3 key={0}>{res.title} {res.matches} / {props.target.props.total}</h3>)
            // return (<h3 key={0}>{res.title} {Math.ceil((res.matches / props.target.props.total) * 100) + "%"}</h3>)
        } else {
            return null
        }
    } else { //pocita sa podla pravidiel kalkulacky
        if (props.target.state.results.length > 0) {
            var tmpRes = props.target.state.results
            tmpRes.sort(function (a, b) { return b.score - a.score })
            return (
                tmpRes.map((res, i) => {
                    return (
                        <div className="channelEntry" key={i}>
                            <h2>
                                <span className="result">{res.score}%</span>
                                <span className="cislo">{res.cislo}</span>
                                <Link to={res.url}>{res.title}</Link>
                            </h2>
                            <p className="space small">{res.summary}</p>
                        </div>
                    )
                })
            )
        } else {
            return null
        }
    }
}
class Results extends Component {
    constructor(props) {
        super(props)
        this.state = {
            results: []
        }
    }
    calculateResults = (props) => {
        if (props.city === "home") return this.calculateQuestionaire(props)
        if (props.answers_compare.length && props.answers.length) {
            var tmpResults = []
            var multiplier = 2
            props.answers_compare.map((kandidat) => {
                var matches = 0, answered = 0, addPoints = 0, addAnswers = 0, score = 0
                kandidat.answers.map((odpoved, i) => {
                    if (typeof props.answers[i] !== 'undefined') {
                        if (typeof props.answers[i]["answerID"] !== 'undefined') {
                            if (odpoved.answerID === 1 || props.answers[i]["answerID"] === 1) {//kandidat neodpovedal alebo ja som neodpovedal
                                //console.log("kandidat " + kandidat.title + " neodpovedal alebo ja som neodpovedal")
                            } else if (odpoved.answerID === props.answers[i]["answerID"]) { //kanddat sa trafil a ja som odpovedal
                                addPoints = props.answers[i]["important"] ? multiplier : 1
                                addAnswers = props.answers[i]["important"] ? multiplier : 1
                                matches += addPoints
                                answered += addAnswers
                                //console.log("kanddat " + kandidat.title + " sa trafil a ja som odpovedal")
                            } else {//kanddat sa netrafil a ja som odpovedal
                                addPoints = props.answers[i]["important"] ? -multiplier : -1
                                addAnswers = props.answers[i]["important"] ? multiplier : 1
                                matches += addPoints
                                answered += addAnswers
                                //console.log("kanddat " + kandidat.title + " sa netrafil a ja som odpovedal")
                            }
                        }
                    }
                    return null
                })
                score = parseInt(((matches / (2 * answered)) + 0.5) * 100, 10)
                tmpResults.push({
                    "title": kandidat.title,
                    "matches": matches,
                    "answered": answered,
                    "url": kandidat.url,
                    "summary": kandidat.summary,
                    "cislo": kandidat.cislo,
                    "score": score
                })
                return null
            })
            return this.setState({ results: tmpResults })
        }
    }
    calculateQuestionaire = (props) => {
        if (props.answers_compare.length && props.answers.length) {
            var tmpResults = []
            var multiplier = 2
            props.answers_compare.map((kandidat) => {
                var matches = 0
                kandidat.answers.map((odpoved, i) => {
                    if (props.answers[i] && odpoved.answerID === props.answers[i]["answerID"]) {
                        var addPoints = props.answers[i]["important"] ? multiplier : 1
                        var newMatch = (matches + addPoints) > props.total ? props.total : (matches + addPoints)
                        matches = newMatch
                    }
                    return null
                })
                tmpResults.push({
                    "title": kandidat.title,
                    "matches": matches,
                    "url": kandidat.url
                })
                return null
            })
            this.setState({ results: tmpResults })
        }
    }

    componentDidMount = () => {
        this.calculateResults(this.props)
    }
    componentWillReceiveProps(nextProps) {
        this.calculateResults(nextProps)
    }

    render() {
        return (
            <div className="content middle">
                {this.state.results.length > 0 && <DisplayResults target={this} city={this.props.city} />}
            </div>
        )
    }
}

export default Results;



