import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Modal from '@material-ui/core/Modal';
import HelpOutlineIcon from '@material-ui/icons/HelpOutline';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

function TabContainer(props) {
    return (
        <div style={{"paddingTop":20}}>
            {props.children}
        </div>
    );
}

function getModalStyle() {
    const top = 50;
    const left = 50;

    return {
        top: `${top}%`,
        left: `${left}%`,
        transform: `translate(-${top}%, -${left}%)`,
    };
}

const styles = theme => ({
    paper: {
        position: 'absolute',
        width: theme.spacing.unit * 50,
        backgroundColor: theme.palette.background.paper,
        // boxShadow: theme.shadows[5],
        paddingLeft: theme.spacing.unit * 4,
        paddingRight: theme.spacing.unit * 4,
        paddingBottom: theme.spacing.unit * 4,
        paddingTop: theme.spacing.unit,
    },
});

class SimpleModal extends React.Component {
    state = {
        open: false,
        value: 0,
    };

    handleChange = (event, value) => {
        this.setState({ value });
    };
    handleOpen = () => {
        this.setState({ open: true });
    };

    handleClose = () => {
        this.setState({ open: false });
    };

    render() {
        const { classes } = this.props;
        const { value } = this.state;
        return (
            <div className="aboutIcon">
                <HelpOutlineIcon onClick={this.handleOpen} />
                <Modal
                    aria-labelledby="simple-modal-title"
                    aria-describedby="simple-modal-description"
                    open={this.state.open}
                    onClose={this.handleClose}
                    className="modal"
                >
                    <div style={getModalStyle()} className={classes.paper}>
                        <Tabs value={value} onChange={this.handleChange}>
                            <Tab label="O projekte" />
                            <Tab label="Partner projektu" />
                        </Tabs>
                        {value === 0 &&
                            <TabContainer>
                                <Typography variant="h6">Občianske združenie CENTRUM KOMUNITNÉHO ORGANIZOVANIA (CKO)</Typography> <br/>podporuje aktívnu participáciu občanov na procese verejného rozhodovania. Pomáha občanom formulovať a obhajovať ich prirodzené záujmy, zároveň vytvára priestor pre efektívnu spoluprácu medzi občanmi, samosprávou a podnikateľským sektorom s cieľom rozvoja komunít.
                                <p>Viac o CKO na <a href="http://www.cko.sk" rel="noopener noreferrer" title="CENTRUM KOMUNITNÉHO ORGANIZOVANIA" target="_blank">www.cko.sk</a></p>
                            </TabContainer>
                        }
                        {value === 1 &&
                            <TabContainer>
                                <Typography variant="h6">Kedros, a.s.  – zaujímavé projekty v oblasti informatizácie spoločnosti</Typography>
                               <img src="/images/kedros.png" alt="Kedros a.s."/><br/> Spoločnosť Kedros a.s.  pôsobí na trhu informačných technológií od roku 2004. Ťažiskom našej činnosti je dodávka riešení a poskytovanie služieb v oblasti informačných systémov. Okrem riešení pre komerčné spoločnosti sme technologickým partnerom riešení aj v iných oblastiach ako riešenia pre samosprávy (napr. mapový portál) či pre neziskové organizácie a takto podporujeme zaujímavé projekty v oblasti informatizácie spoločnosti.
                               <p>Viac o Kedros a.s. na <a href="https://www.kedros.sk" rel="noopener noreferrer" title="Kedros a.s." target="_blank">www.kedros.sk</a></p>
                            </TabContainer>
                        }
                    </div>
                </Modal>
            </div>
        );
    }
}

SimpleModal.propTypes = {
    classes: PropTypes.object.isRequired,
};

const AboutModal = withStyles(styles)(SimpleModal);

export default AboutModal;



